.catalog__container {
  margin: 27px 20px 25px 20px;
}

.catalog__title-block {
  padding-bottom: 49px;
}

.catalog__title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #000000;
  margin: 0;
}

.card__image-wrapper {
  min-width: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.catalog__list {
  margin-top: 0;
  padding-left: 0;
  list-style-type: none;
}

.card__block {
  display: flex;
  min-height: 112px;
  padding: 11px 0 13px 0;
}

.card__link-block {
  margin-bottom: 18px;
  width: max-content;
}

.card__title-link {
  display: flex;
  text-decoration: none;
}

.card__title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  align-items: center;
  text-transform: uppercase;
  color: #222222;
  margin: 0;
}

.card__data {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #444444;
}

.card__table {
  min-width: 140px;
}

.card__table-item {
  display: flex;
  justify-content: space-between;
}

.card__button-block {
  padding-bottom: 14px;
}

.card__button {
  min-height: 40px;
  display: flex;
  justify-content: center;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
}

.catalog__item {
  margin-bottom: 19px;
}

.catalog__more-wrapper {
  min-height: 158px;
  padding: 17px 0 0 0;
}

.catalog__more-text {
  min-height: 118px;
}

.catalog__more-link-block {
  margin-bottom: 23px;
}

.catalog__more-link {
  display: flex;
  list-style-type: none;
  background-image: url(../img/catalog/plus.svg);
  margin: 0 auto;
  width: 30px;
  height: 30px;
}

.cards-more__item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #111111;
}

.cards-more__text {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #444444;
  margin: 0;
  padding-top: 8px;
}

.card__more-block {
  padding-bottom: 0;
}

.card__more-button {
  color: #444444;
  background: #f2f2f2;
}
